/* This is where the css file for the design page is stored */

.designHero {
	background: url('../Assets/welcome.png') center center no-repeat fixed;
	background-size: 100vw 100vh;
	padding: 1.5rem 2rem;
}

.designHero-header {
	display: flex;
}

.designHero-h1 {
	font-size: 2rem;
	font-weight: bold;
}

.designHero-text {
	font: 1.5rem;
	margin-top: 0.5rem;
	max-width: 80%;
}

.designHero-svg {
	width: 3rem;
	height: 3rem;
	align-self: flex-end;
	bottom: 0;
}
