.App {
	box-sizing: border-box;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}

/* import fonts  */
@font-face {
	font-family: 'Ogg';
	src: url(./fonts/ogg/Ogg-Bold.woff) format('woff');
	font-weight: bold;
}

@font-face {
	font-family: 'Ogg';
	src: url(./fonts/ogg/Ogg-Regular.woff) format('woff');
}

@font-face {
	font-family: 'Ogg';
	src: url(./fonts/ogg/OggText-Medium.woff) format('woff');
	font-weight: 600;
}

@font-face {
	font-family: 'Product Sans';
	src: url(./fonts/productSans/Product\ Sans\ Regular.ttf) format('ttf');
}

@font-face {
	font-family: 'Product Sans';
	src: url(./fonts/productSans/Product\ Sans\ Bold.ttf) format('ttf');
	font-weight: 600;
}

body {
	font-family: 'Ogg';
}
